import React from 'react';
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip
} from 'recharts';

function Chart({ title, data }) {
  return(
    <div>
      <span style={{ fontWeight: 'bold' }}>{ title }</span>
      <LineChart
        width={ 600 }
        height={ 300 }
        data={ data }
        margin={{
          top: 5, right: 30, left: 20, bottom: 5
        }}
      >
        <CartesianGrid />
        <XAxis dataKey='time' />
        <YAxis dataKey='value' />
        <Tooltip />
        <Line type='monotone' dataKey='value' stroke='#8884d8' activeDot={{ r: 8 }} />
      </LineChart>
    </div>
  );
}

export default Chart;