import _ from 'lodash';

import SCHEMAS from './schemas';
import {
  RTCInboundRTPAudioStream,
  RTCOutboundRTPAudioStream,
  RTCInboundRTPVideoStream,
  RTCOutboundRTPVideoStream,
  RTCIceCandidate,
  RTCIceCandidatePair,
} from '../constants';
import { timestampToMiliseconds } from '../utils';

let results = resetResults();
let samplingPeriod = 0;

function resetResults() {
  // this is to keep the keys in a particular order in UI
  // if ever need to change this order, do it here
  // it will take effect in UI
  return {
    [RTCInboundRTPAudioStream]: null,
    [RTCOutboundRTPAudioStream]: null,
    [RTCInboundRTPVideoStream]: null,
    [RTCOutboundRTPVideoStream]: null,
    [RTCIceCandidatePair]: null,
    [RTCIceCandidate]: null
  };
}

function saveChartData(RTCKey, data, fullData) {
  const SCHEMA = SCHEMAS[RTCKey]();
  const state = {};
  
  if (!results[RTCKey]) {
    results[RTCKey] = SCHEMA.schema;
    results[RTCKey].startTime = data.timestampUs || data.timestamp;
  }

  if (!results[RTCKey].streamIds.includes(data.id)) {
    SCHEMA.perSecondCalculationKeys.forEach(item => {
      results[RTCKey][item.totalValueKey] = _.last(results[RTCKey][item.key]) || 0;
    });
  }

  SCHEMA.perSecondCalculationKeys.forEach(item => {
    state[item.key] = data[item.key];

    if (results[RTCKey][item.totalValueKey] !== 0) {
      state[item.key] += results[RTCKey][item.totalValueKey];
    }

    const timeStamp = data.timestampUs || data.timestamp;
    const perSecondValue = calculatePerSecondValue({
      currentValue: state[item.key],
      previousValue: _.last(results[RTCKey][item.key]) || 0,
      currentTime: timeStamp,
      previousTime: results[RTCKey].endTime || (timestampToMiliseconds(timeStamp) - (samplingPeriod * 1000))
    });
  
    if (_.isFinite(perSecondValue)) {
      results[RTCKey][item.perSecondKey].push(perSecondValue);
    }

    results[RTCKey][item.key].push(state[item.key]);
  });

  SCHEMA.standartKeys.forEach(item => {
    item.type === 'array'
      ? results[RTCKey][item.key].push(data[item.key] || item.defaultValue)
      : (results[RTCKey][item.key] = data[item.key] || item.defaultValue);
  });

  SCHEMA.exceptionKeys.forEach(item => {
    let objectByKey = null;
    let valueToSave = null;

    if (item.hasLinkedKey) {
      objectByKey = getItemFromCollectionByKey(fullData, data[item.linkedKey]);
      if (objectByKey) {
        valueToSave = _.get(
          objectByKey,
          item.exceptionKey,
          item.exceptionKey2
            ? objectByKey[item.exceptionKey2]
            : objectByKey[item.key]
        );

        item.type === 'array'
          ? results[RTCKey][item.key].push(valueToSave)
          : (results[RTCKey][item.key] = valueToSave);
      }
    } else {
      valueToSave = _.get(data, item.exceptionKey, data[item.exceptionKey2]);

      item.type === 'array'
        ? results[RTCKey][item.key].push(valueToSave)
        : (results[RTCKey][item.key] = valueToSave);
    }
  });
}

function saveConstData(RTCKey, data) {
  const SCHEMA = SCHEMAS[RTCKey]();

  if (!(results[RTCKey] && results[RTCKey][data.id])) {
    results[RTCKey] = Object.assign({}, results[RTCKey], {[data.id]: SCHEMA.schema});
    results[RTCKey][data.id].startTime = data.timestampUs || data.timestamp;
  }

  SCHEMA.standartKeys.forEach(item => {
    item.type === 'array'
      ? results[RTCKey][data.id][item.key].push(
          data[item.key] || item.defaultValue
        )
      : (results[RTCKey][data.id][item.key] =
          data[item.key] || item.defaultValue);
  });

  SCHEMA.exceptionKeys.forEach(item => {
    const valueToSave = _.get(data, item.exceptionKey, data[item.exceptionKey2] || item.defaultValue);

    item.type === 'array'
      ? results[RTCKey][data.id][item.key].push(valueToSave)
      : (results[RTCKey][data.id][item.key] = valueToSave);
  });
}

function handleFileSelection (file, setSdkInfo, setParsedData) {
  const fileReader = new FileReader();

  fileReader.onload = loadEvent => {
    results = resetResults();
    handleFileContentParsing(loadEvent.target.result, setSdkInfo, setParsedData);
  }

  fileReader.onerror = () => {
    console.warn(fileReader.error);
  }

  fileReader.readAsText(file);
}

function handleFileContentParsing(fileContent, setSdkInfo, setParsedData) {
  // if the provided content is in JSON format
  // then it's most probably a JS SDK file
  // if not, treat it as MobileSDK file
  try {
    const parsedData = JSON.parse(fileContent);

    if (parsedData.length > 0) {
      const allLogItems = parsedData[parsedData.length - 1][1];

      // JS SDK provides an item starting with "kandy-sdk-info"
      // every time it produces new logs
      const allSdkInfoItems = allLogItems.filter(item => item.id.includes('kandy-sdk-info'));

      // first and second items are needed to get samplingPeriod
      // last item is needed to get endTime
      const sdkInfo = getSDKInfo(allSdkInfoItems[0], allSdkInfoItems[1], allSdkInfoItems[allSdkInfoItems.length - 1]);

      setSdkInfo(sdkInfo);
      JSSDKParser(allLogItems, setParsedData);
    }
  } catch (error) {
    mobileSDKParser(fileContent, setSdkInfo, setParsedData);
  }
}

function mobileSDKParser(fileContent, setSdkInfo, setParsedData) {
  let dataList = fileContent.trim().split('\n');
  let sdkInfo = null;

  dataList = dataList.filter(item => !!item);

  dataList.forEach(item => {
    try {
      const jobj = JSON.parse(item);
      const keys = Object.keys(jobj);

      if (!sdkInfo) {
        const secondObj = JSON.parse(dataList[1]);
        const lastObj = JSON.parse(dataList[dataList.length - 1]);

        // first and second items are needed to get samplingPeriod
        // last item is needed to get endTime
        sdkInfo = getSDKInfo(jobj['kandy-sdk-info'], secondObj['kandy-sdk-info'], lastObj['kandy-sdk-info']);
        setSdkInfo(sdkInfo);
      }

      keys.forEach(key => {
        // here we're moving the props under members to the root
        // so it will be similar to JS SDK
        let newObj = { ...jobj[key], ...jobj[key].members };
        delete newObj.members;
        saveData(key, newObj, jobj);
      });
    } catch (error) {
      console.warn(error);
    }
  });

  setParsedData(results);
  console.log(results);
}

function JSSDKParser(parsedData, setParsedData) {
  parsedData.forEach(item => {
    saveData(item.id, item, parsedData);
  });

  setParsedData(results);
  console.log(results);
}

function saveData(key, data, fullData) {
  switch (true) {
    case key.includes(RTCInboundRTPAudioStream):
      saveChartData(RTCInboundRTPAudioStream, data, fullData);
      break;
    case key.includes(RTCOutboundRTPAudioStream):
      saveChartData(RTCOutboundRTPAudioStream, data, fullData);
      break;
    case key.includes(RTCInboundRTPVideoStream):
      saveChartData(RTCInboundRTPVideoStream, data, fullData);
      break;
    case key.includes(RTCOutboundRTPVideoStream):
      saveChartData(RTCOutboundRTPVideoStream, data, fullData);
      break;
    // the order of the next two cases is very important
    // do not change it unless you know what you're doing
    case key.includes(RTCIceCandidatePair):
      saveConstData(RTCIceCandidatePair, data);
      break;
    case key.includes(RTCIceCandidate):
      saveConstData(RTCIceCandidate, data);
      break;
  }
}

function getSDKInfo(firstSdkInfoObj, secondSdkInfoObj, lastSdkInfoObj) {
  let _sdkInfoObj = Object.assign({}, firstSdkInfoObj);

  // mobileSDK has the values under members
  // move the values to the object root
  if (_sdkInfoObj && _sdkInfoObj.members) {
    _sdkInfoObj = { ...firstSdkInfoObj, ..._sdkInfoObj.members };
    delete _sdkInfoObj.members;
  }

  const { sdk, version, callId, platform, timestampUs, timestamp } = _sdkInfoObj;

  // get timestamp value from the very last item in the logs
  // to have endTime
  const { timestampUs: _timestampUs, timestamp: _timestamp } = lastSdkInfoObj;

  // different platforms have different names for timestamp
  // get available one
  const startTime = timestampUs || timestamp;
  const endTime = _timestampUs || _timestamp;

  const _samplingPeriod = 
    timestampToMiliseconds(secondSdkInfoObj.timestampUs || secondSdkInfoObj.timestamp) -
    timestampToMiliseconds(firstSdkInfoObj.timestampUs || firstSdkInfoObj.timestamp);

  samplingPeriod = !isNaN(_samplingPeriod)
    ? Math.round(_samplingPeriod / 1000)
    : 0;

  return {
    sdk,
    version,
    callId,
    platform,
    startTime,
    endTime,
    samplingPeriod
  };
}

function calculatePerSecondValue({ currentValue, previousValue, currentTime, previousTime }) {
  if (!currentValue) return 0;

  const deltaValue = currentValue - previousValue;
  const deltaTime = timestampToMiliseconds(currentTime) - timestampToMiliseconds(previousTime);
  
  return (deltaValue / (deltaTime / 1000));
}

function getItemFromCollectionByKey(dataList, key) {
  // JS SDK
  if (Array.isArray(dataList)) {
    return dataList.find(item => item.id === key);
  
  // MobileSDK
  } else {
    return dataList[key];
  }
}

export {
  handleFileSelection
}