import React, { Component } from 'react';
import { Collapse, Descriptions, Row, Divider } from 'antd';
import _ from 'lodash';

import Chart from './chart';
import {
  RTCIceCandidatePair,
  RTCIceCandidate
} from '../constants';
import SCHEMAS from '../parser/schemas';
import {
  timestampToMiliseconds,
  timestampToLocalTime,
  isLikelyTimeStamp,
} from '../utils';

const { Panel } = Collapse;

class DataContainer extends Component {
  state = {
    localCandidateId: null,
    remoteCandidateId: null,
    mainCollapseActiveKeys: [],
    iceCandidateActiveKeys: []
  }

  componentDidUpdate(prevProps) {
    if (!_.isEqual(this.props.descriptionList, prevProps.descriptionList)) {
      this.setState({
        localCandidateId: null,
        remoteCandidateId: null,
        mainCollapseActiveKeys: [],
        iceCandidateActiveKeys: []
      });
    }
  }

  render() {
    const { mainCollapseActiveKeys } = this.state;

    return(
      <Collapse
        activeKey={ mainCollapseActiveKeys }
        onChange={ activeKeys => this.setState({mainCollapseActiveKeys: [...activeKeys]}) }
      >
        { this._getCharts() }
        { this._getIceCandidatePairs() }
        { this._getIceCandidates() }
      </Collapse>
    );
  }

  _getCharts() {
    const { chartList } = this.props;

    if(_.isEmpty(chartList))
      return null;

    return chartList.map(chartData => {
      const result = {};
      const hiddenKeys = SCHEMAS[chartData[0]]().hiddenKeys;
      const _charts = Object.entries(chartData[1]).filter(item => Array.isArray(item[1]) && !hiddenKeys.includes(item[0]));
      const _chartDescriptions = Object.entries(chartData[1]).filter(item => !Array.isArray(item[1]) && !hiddenKeys.includes(item[0]));
  
      result['charts'] = _charts.map(chart => {
        const _chartData = chart[1];
        const startTime = timestampToMiliseconds(chartData[1].startTime);
        const endTime = timestampToMiliseconds(chartData[1].endTime);
        const finalChartData = [];
  
        const timeDiff = endTime - startTime;
        const timeSpan = timeDiff / _chartData.length;
  
        _chartData.forEach((value, i) => {
          const date = new Date(startTime + (timeSpan * (i + 1)));
  
          finalChartData.push({
            'time': timestampToLocalTime(date.getTime()),
            'value': value
          });
        });
  
        return (
          <Chart title={ chart[0] } data={ finalChartData } />
        );
      });
  
      result['chartDescriptions'] = _chartDescriptions.map(description => {
        return (
          <Descriptions.Item label={ description[0] } labelStyle={{ fontWeight: 'bold' }}>
            { isLikelyTimeStamp(description[1]) ? timestampToLocalTime(timestampToMiliseconds(description[1])) : description[1] }
          </Descriptions.Item>
        );
      });
  
      return (
        <Panel header={ chartData[0] } key={ chartData[0] }>
          <Row style={{ justifyContent: 'center' }}>
            <Descriptions style={{ padding: 10}} column={{ xs: 1, sm: 2, lg: 4 }}>
              { result['chartDescriptions'] }
            </Descriptions>
          </Row>
          <Divider>Charts</Divider>
          <Row style={{ justifyContent: 'center' }}>
            { result['charts'] }
          </Row>
        </Panel>
      );
    });
  }

  _getIceCandidatePairs() {
    const { descriptionList } = this.props;
    const { localCandidateId, remoteCandidateId } = this.state;
    
    if (_.isEmpty(descriptionList))
      return null;

    let iceCandidatePairList = descriptionList.find(item => item[0] === RTCIceCandidatePair)[1];
    const iceCandidatePairArray = Object.entries(iceCandidatePairList);
    let nominatedCandidatePairIndex = 0;
    const nominatedCandidatePair = iceCandidatePairArray.find((item, i) => {
      if (item[1].nominated) {
        nominatedCandidatePairIndex = i;
        return true;
      }
      return false;
    });
    const candidateIds = {
      localCandidateId: nominatedCandidatePair[1].localCandidateId,
      remoteCandidateId: nominatedCandidatePair[1].remoteCandidateId
    };

    delete iceCandidatePairArray[nominatedCandidatePairIndex];
    iceCandidatePairArray.unshift(nominatedCandidatePair);

    if (!localCandidateId && !remoteCandidateId) {
      this.setState(candidateIds);
    }

    return(
      <Panel header={ RTCIceCandidatePair } key={ RTCIceCandidatePair }>
        <Collapse>
          {
            iceCandidatePairArray.map(iceCandidatePair => {
              const iceCandidatePairKey = iceCandidatePair[0];
              const iceCandidatePairData = iceCandidatePair[1];

              return (
                <Panel
                  header={ this._getPanelHeaderStyle({ isBold: iceCandidatePairData.nominated, headerText: iceCandidatePairKey }) }
                  key={ iceCandidatePairKey }
                >
                  <Descriptions style={{ padding: 10}} column={{ xs: 1, sm: 2, lg: 4 }}>
                    {
                      Object.entries(iceCandidatePairData).map(entry => {
                        return (
                          <Descriptions.Item label={ entry[0] } labelStyle={{ fontWeight: 'bold' }}>
                            { 
                              isLikelyTimeStamp(entry[1]) ?
                              timestampToLocalTime(timestampToMiliseconds(entry[1])) :
                              (['localCandidateId', 'remoteCandidateId'].includes(entry[0]) ?
                              (<a
                                  onClick={ () => this._onClickIceCandidatePairLink(entry[1]) }
                                  href={ `#${entry[1]}` }>{ String(entry[1]) }
                                </a>) :
                              String(entry[1]))
                            }
                          </Descriptions.Item>
                        );
                      })
                    }
                  </Descriptions>
                </Panel>
              );
            })
          }
        </Collapse>
      </Panel>
    );
  }

  _getIceCandidates() {
    const { descriptionList } = this.props;
    const {
      localCandidateId,
      remoteCandidateId,
      iceCandidateActiveKeys
    } = this.state;
    
    if (_.isEmpty(descriptionList))
      return null;

    const iceCandidateList = descriptionList.find(item => item[0] === RTCIceCandidate)[1];
    let iceCandidateArray = Object.entries(iceCandidateList);
    const notNominatedCandidates = [];
    const nominatedCandidates = [];

    iceCandidateArray.forEach(item => {
      if ([localCandidateId, remoteCandidateId].includes(item[0])) {
        nominatedCandidates.push(item);
      } else {
        notNominatedCandidates.push(item);
      }
    });

    iceCandidateArray = [...nominatedCandidates, ...notNominatedCandidates];

    return(
      <Panel header={ RTCIceCandidate } key={ RTCIceCandidate }>
        <Collapse
          activeKey={ iceCandidateActiveKeys }
          onChange={ (activeKeys) => this.setState({iceCandidateActiveKeys: [...activeKeys]}) }
        >
          {
            iceCandidateArray.map(iceCandidate => {
              const iceCandidateKey = iceCandidate[0];
              const iceCandidateData = iceCandidate[1];

              return (
                <Panel
                  header={this._getPanelHeaderStyle({ isBold: [localCandidateId, remoteCandidateId].includes(iceCandidateKey), headerText: iceCandidateKey })}
                  key={ iceCandidateKey }
                  id={ iceCandidateKey }
                >
                  <Descriptions style={{ padding: 10}} column={ { xs: 1, sm: 2, lg: 4 } }>
                    {
                      Object.entries(iceCandidateData).map(entry => {
                        return (
                          <Descriptions.Item label={ entry[0] } labelStyle={{ fontWeight: 'bold' }}>
                            { isLikelyTimeStamp(entry[1]) ? timestampToLocalTime(timestampToMiliseconds(entry[1])) : String(entry[1]) }
                          </Descriptions.Item>
                        );
                      })
                    }
                  </Descriptions>
                </Panel>
              );
            })
          }
        </Collapse>
      </Panel>
    );
  }

  _getPanelHeaderStyle({ isBold, headerText }) {
    return(
      <span style={{ fontWeight: isBold ? 'bold' : 'normal' }}>
        { headerText }
      </span>
    );
  }

  _onClickIceCandidatePairLink(activeKey) {
    const { mainCollapseActiveKeys, iceCandidateActiveKeys } = this.state;

    const isAdded = mainCollapseActiveKeys.indexOf(RTCIceCandidate);
    let newState = {
      iceCandidateActiveKeys: [activeKey, ...iceCandidateActiveKeys]
    };

    if (isAdded === -1) {
      newState = {
        ...newState,
        mainCollapseActiveKeys: [RTCIceCandidate, ...mainCollapseActiveKeys]
      };
    }

    this.setState(newState);
  }
}

export default DataContainer;