import { useState } from 'react';
import { Layout, Upload, Descriptions, Divider } from 'antd';
import { InboxOutlined } from '@ant-design/icons';

import 'antd/dist/antd.css';

import DataContainer from './components/dataContainer';
import { handleFileSelection } from './parser/parser';
import { timestampToMiliseconds, timestampToLocalDateTime } from './utils';
import {
  RTCIceCandidatePair,
  RTCIceCandidate,
  RTCInboundRTPAudioStream,
  RTCOutboundRTPAudioStream,
  RTCInboundRTPVideoStream,
  RTCOutboundRTPVideoStream,
  sdkInfoSchema
} from './constants';

const CHARTS = [
  RTCInboundRTPAudioStream,
  RTCOutboundRTPAudioStream,
  RTCInboundRTPVideoStream,
  RTCOutboundRTPVideoStream,
];
const DESCRIPTIONS = [RTCIceCandidatePair, RTCIceCandidate];

const { Content } = Layout;
const { Dragger } = Upload;

function App() {
  const [parsedData, setParsedData] = useState(null);
  const [sdkInfo, setSdkInfo] = useState(null);

  const draggerProps = {
    name: 'file',
    showUploadList: false,
    customRequest(info) {
      console.log('Override default xhr implementation: ', info);
    },
    onChange(info) {
      const { file } = info;
      handleFileSelection(file.originFileObj, setSdkInfo, setParsedData);
    }
  };



  const chartList = Object.entries(parsedData || {}).filter(item => item[1] && CHARTS.includes(item[0]));
  const descriptionList = Object.entries(parsedData || {}).filter(item => item[1] && DESCRIPTIONS.includes(item[0]));

  return (
    <Layout>
      <Content style={{ backgroundColor: "white" }}>
        <Dragger {...draggerProps}>
          <p className="ant-upload-drag-icon">
            <InboxOutlined />
          </p>
          <p className="ant-upload-text">
            Click or drag file to this area to upload
          </p>
        </Dragger>

        {sdkInfo ? (
          <>
            <Divider>SDK Info</Divider>
            <Descriptions
              style={{ padding: 10 }}
              column={{ xs: 1, sm: 2, lg: 4 }}
            >
              {sdkInfoSchema.map((item) => {
                return (
                  <Descriptions.Item
                    label={item.text}
                    labelStyle={{ fontWeight: "bold" }}
                  >
                    {item.isDate
                      ? timestampToLocalDateTime(
                          timestampToMiliseconds(sdkInfo[item.key])
                        )
                      : sdkInfo[item.key] || ""}
                  </Descriptions.Item>
                );
              })}
            </Descriptions>
          </>
        ) : null}

        {parsedData ? (
          <>
            <Divider>Charts and Statistics Info</Divider>
            <DataContainer {...{ chartList, descriptionList }} />
          </>
        ) : null}
      </Content>
    </Layout>
  );
}

export default App;
