export const timestampToMiliseconds = timestamp => (timestamp > (10 ** 15)) ? Math.ceil(timestamp / 1000) : timestamp;

export const timestampToLocalTime = timestamp => (new Intl.DateTimeFormat(navigator.languages[0], { timeStyle: 'medium', hour12: false }).format(new Date(timestamp)));

export const timestampToLocalDateTime = timestamp => (new Intl.DateTimeFormat(navigator.languages[0], { dateStyle: 'medium', timeStyle: 'medium', hour12: false }).format(new Date(timestamp)));

export const isLikelyTimeStamp = value => !isNaN(value) && (String(value).length >= 13);
